          animation-delay: 0;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
}

@-webkit-keyframes slideIn {
  50% {
    -webkit-transform: translateX(125px);
            transform: translateX(125px);
  }

  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@keyframes slideIn {
  50% {
    -webkit-transform: translateX(125px);
            transform: translateX(125px);
  }

  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
